import { useEffect, useState } from "react";
import monthsData from "./monthData";
import Header from "./components/Header";
import OpensDateChart from "./components/OpensDateChart";
import OsPieChart from "./components/OsPieChart";
import UserCountriesBars from "./components/UserCountriesBars";
import VersionsBars from "./components/VersionsBars";
import { ActiveUser, MonthData } from "./interfaces";
import moment from "moment";
import axios from "axios";
import colors from "./colors";

const App = () => {

    /// Mes seleccionado actualmente
    const [monthData, setMonthData] = useState<MonthData | null>(null);

    /// Trae el json con los datos del mes seleccionado y parsea los usuarios activos
    const changeData = async (month: MonthData) => {
        try {
            setMonthData(null);

            const res = await axios.get(month.jsonPath);

            /// A partir de noviembre 2022 se modificó el formato de los datos, se agrego la fecha 
            /// de actualizacion de los datos
            const actives: ActiveUser[] = res.data?.users ?? res.data;

            if (res.data?.users) {
                month.updated = moment(res.data?.date);
            }

            month.users = [];

            month.users = actives
                .map((e: any) => ({
                    country: e.country,
                    lastOpen: moment(e.lastOpen, 'x'),
                    opens: e.opens,
                    opensByDate: e.opensByDate,
                    os: e.os,
                    version: e.v,
                }));

            /// Filtra los usuarios que no han abierto la app en el mes seleccionado
            /// A partir de noviembre 2022 el filtro se hace en el parser
            if(!month.updated || (month.updated && month.updated.isBefore(moment('2022-11-01')))) { 
                month.users = month.users.filter(e => e.lastOpen.isBetween(month.start, month.finish, "day", "[]"));
            }

            setMonthData(month);

        } catch (e) {
            console.log(e)
        }
    }

    useEffect(() => {
        changeData(monthsData[0]);
    }, []);

    if (!monthData) return (
        <div className="h-screen flex justify-center items-center">
            <div role="status">
                <svg className="inline mr-2 w-32 h-32 text-gray-200 animate-spin dark:text-gray-200" style={{ fill: colors.blue }} viewBox="0 0 100 101" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z" fill="currentColor" />
                    <path d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z" fill="currentFill" />
                </svg>
            </div>
        </div>
    );

    return (
        <div className="max-w-screen-md lg:max-w-screen-lg mx-auto bg-gray-100 flex flex-col h-screen">
            <Header monthData={monthData} setData={changeData} />
            <div className="grid grid-cols-12 p-4 flex-1 overflow-y-auto">
                <div className="col-span-12 md:col-span-4">
                    <OsPieChart monthData={monthData} />
                </div>
                <div className="col-span-12 md:col-span-8">
                    <VersionsBars monthData={monthData} />
                </div>
                <div className="col-span-12 mt-4">
                    <OpensDateChart monthData={monthData} />
                </div>
                <div className="col-span-12 mt-4">
                    <UserCountriesBars monthData={monthData} />
                </div>
            </div>
        </div>
    );
};

export default App;
